import { Component, OnInit } from '@angular/core';
import { CurriculumService } from './curriculum.service';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./curriculum-manage.component.html",
    providers: [CurriculumService]
})
export class CurriculumDeleteComponent implements OnInit {

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public curriculumService: CurriculumService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ curriculum }) => {
            this.deleteCurriculum(curriculum);
        });
    }

    protected subscribeToDeleteCurriculumResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteCurriculumSuccess(res), (res: HttpErrorResponse) => this.onDeleteCurriculumError(res));
    }

    //Delete curriculum
    deleteCurriculum(curriculum) {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this curriculum ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(curriculum.id);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(id: number) {
        this.subscribeToDeleteCurriculumResponse(this.curriculumService.delete(id));
    }

    previousUrl() {
        this.router.navigateByUrl('/curriculum');
    }

    protected onDeleteCurriculumSuccess(resp) {
        this.pluginService.alertMessage("Curriculum deleted successfully", "success");
        this.previousUrl();
    }

    onDeleteCurriculumError(err: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not delete curriculum, please contact admin", "error")
    }

}