import { Injectable } from '@angular/core';
import { ICurriculum, Curriculum } from '../shared/model/content/curriculum.model';
import { CurriculumService } from './curriculum.service';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, Routes } from '@angular/router';
import { Observable, of } from 'rxjs';
import { HttpResponse } from '@angular/common/http';
import { filter, map } from 'rxjs/operators';
import { CurriculumManageComponent } from './curriculum-manage.component';
import { UserRouteAccessService } from '../core';
import { CurriculumCreateComponent } from './curriculum-create.component';
import { CurriculumUpdateComponent } from './curriculum-update.component';
import { CurriculumDetailsComponent } from './curriculum-details.component';
import { CurriculumPublishComponent } from './curriculum-update-publish.component';
import { CurriculumUnpublishComponent } from './curriculum-update-unpublish.component';
import { CurriculumDeleteComponent } from './curriculum-delete.component';
import { CurriculumAffiliationsComponent } from './curriculum-affiliations.component';

@Injectable({ providedIn: 'root' })
export class CurriculumResolve implements Resolve<ICurriculum> {
    constructor(private service: CurriculumService) { }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ICurriculum> {
        const id = route.params['id'] ? route.params['id'] : null;
        if (id) {
            return this.service.find(id).pipe(
                filter((response: HttpResponse<Curriculum>) => response.ok),
                map((curriculum: HttpResponse<Curriculum>) => curriculum.body)
            );
        }
        return of(new Curriculum());
    }
}

export const curriculumRoute: Routes = [
    {
        path: '',
        component: CurriculumManageComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Manage curriculums'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: 'create',
        component: CurriculumCreateComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Create new curriculum',
            action: "create"
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/edit',
        component: CurriculumUpdateComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Edit curriculum',
            action: 'edit'
        },
        canActivate: [UserRouteAccessService]
    }, {
        path: ':id/view',
        component: CurriculumDetailsComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'View curriculum',
            action: 'view'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/delete',
        component: CurriculumDeleteComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Delete curriculum',
            action: 'delete'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/publish',
        component: CurriculumPublishComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Publish curriculum',
            action: 'publish'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/unpublish',
        component: CurriculumUnpublishComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Unpublish curriculum',
            action: 'unpublish'
        },
        canActivate: [UserRouteAccessService]
    }, {
        path: ':id/affiliations',
        component: CurriculumAffiliationsComponent,
        resolve: {
            curriculum: CurriculumResolve
        },
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Manage curriculum affiliations',
            action: 'unpublish'
        },
        canActivate: [UserRouteAccessService]
    }
]