import { Injectable } from '@angular/core';
import { HttpResponse, HttpHeaders, HttpClient } from '@angular/common/http';
import { ICurriculum } from '../shared/model/content/curriculum.model';
import { environment } from '../../environments/environment';
import { createRequestOption } from '../shared';
import { Observable } from 'rxjs';
import { IOrganisation } from '../shared/model/common/organisation.model';
import { ACTIVE } from '../shared/constants/common.constants';

type EntityResponseType = HttpResponse<ICurriculum>;
type EntityArrayResponseType = HttpResponse<ICurriculum[]>;
type OrgEntityArrayResponseType = HttpResponse<IOrganisation[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');


@Injectable({ providedIn: 'root' })
export class CurriculumService {

    public curriculumResourceUrlSearch = environment.API_URL + 'services/learning/api/curricula/search';
    public curriculumResourceUrl = environment.API_URL + 'services/learning/api/curricula';

    constructor(protected http: HttpClient) { }

    createCurriculum(curriculum: ICurriculum): Observable<EntityResponseType> {
        return this.http.post<ICurriculum>(this.curriculumResourceUrl, curriculum, { observe: 'response' });
    }

    updateCurriculum(curriculum: ICurriculum): Observable<EntityResponseType> {
        return this.http.put<ICurriculum>(this.curriculumResourceUrl, curriculum, { observe: 'response' });
    }

    find(id: number): Observable<EntityResponseType> {
        return this.http.get<ICurriculum>(`${this.curriculumResourceUrl}/${id}`, { observe: 'response' });
    }

    getStandardCurriculums(req?: any): Observable<EntityArrayResponseType> {
        const options = createRequestOption(req);
        var reqJSON = {
            "isExtraCurriculum": false
        };
        return this.http.post<ICurriculum[]>(this.curriculumResourceUrlSearch, reqJSON, { params: options, observe: 'response' });
    }

    getCurriculumSearch(req?: any): Observable<EntityArrayResponseType> {
        const options = createRequestOption(req);
        var reqJSON = {
            "isExtraCurriculum": false,
            "status": ACTIVE
        };
        return this.http.post<ICurriculum[]>(this.curriculumResourceUrlSearch, reqJSON, { params: options, observe: 'response' });
    }

    delete(id: number): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.curriculumResourceUrl}/${id}`, { observe: 'response' });
    }
}
