import { Component, OnInit } from '@angular/core';
import { CurriculumService } from './curriculum.service';
import { ICurriculum } from '../shared/model/content/curriculum.model';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { INACTIVE } from '../shared/constants/common.constants'
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';

@Component({
    templateUrl: "./curriculum-manage.component.html",
    providers: [CurriculumService]
})
export class CurriculumUnpublishComponent implements OnInit {

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public curriculumService: CurriculumService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ curriculum }) => {
            this.unpublishCurriculum(curriculum);
        });
    }

    protected subscribeToSaveResponse(result: Observable<HttpResponse<ICurriculum>>) {
        result.subscribe((res: HttpResponse<ICurriculum>) => this.onSaveSuccess(res), (errRes: HttpErrorResponse) => this.onSaveError(errRes));
    }

    unpublishCurriculum(curriculum) {
        curriculum.status = INACTIVE;
        this.subscribeToSaveResponse(this.curriculumService.updateCurriculum(curriculum));
        this.pluginService.alertMessage("Curriculum published successfully", "success")
        this.router.navigateByUrl('/curriculum');
    }


    onSaveSuccess(resp: HttpResponse<ICurriculum>) {
        if (resp.body.status === INACTIVE) {
            this.pluginService.alertMessage("Curriculum unpublished successfully", "success")
        }
    }

    onSaveError(errRes: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not publish curriculum, please contact admin", "error")
    }
}