
export interface ICurriculum {
  id?: number;
  acronym?: string,
  country?: string,
  description?: string,
  endDate?: string,
  gradesApplicableFrom?: number,
  gradesApplicableTo?: number,
  isExtraCurriculum?: boolean,
  name?: string,
  curriculumTypeId?: number;
  organisationId?: number,
  startDate?: string,
  state?: string,
  status?: string,
  checked?: boolean
}



export class Curriculum implements ICurriculum {
  constructor(
    public id?: number,
    public acronym?: string,
    public country?: string,
    public description?: string,
    public endDate?: string,
    public gradesApplicableFrom?: number,
    public gradesApplicableTo?: number,
    public isExtraCurriculum?: boolean,
    public name?: string,
    public organisationId?: number,
    public curriculumTypeId?: number,
    public startDate?: string,
    public state?: string,
    public status?: string,
    public checked?: boolean) {
    this.checked = false;
  }
}
