import { OnInit, Component } from '@angular/core';
import { CurriculumService } from './curriculum.service';
import { Router, ActivatedRoute } from '@angular/router';
import { JhiEventManager } from 'ng-jhipster';
import { PluginService } from '../shared/plugin/plugin.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ICurriculum } from '../shared/model/content/curriculum.model';
import { ICountry } from '../shared/model/common/country.model';
import { CommonService } from '../shared/common/common.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./curriculum-details.component.html",
    providers: [CurriculumService]
})
export class CurriculumDetailsComponent implements OnInit {
    curriculumForm: FormGroup;
    curriculum: ICurriculum = {};
    countryList: ICountry[];
    curriculumId: number;
    startDate: string;
    endDate: string;
    country: string;

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public curriculumService: CurriculumService,
        protected eventManager: JhiEventManager,
        public pluginService: PluginService,
        public formBuilder: FormBuilder,
        public commonService: CommonService
    ) {
    }

    ngOnInit() {
        this.pluginService.sideMenuToggle();
        this.activatedRoute.data.subscribe(({ curriculum }) => {
            this.curriculum = curriculum;
            this.getCountryList();
        });
    }

    //Get country list from local JSON data
    getCountryList() {
        this.commonService.getCountryList().subscribe((countryData: any) => {
            this.countryList = countryData;
            this.mapCurriculumValues();
        });
    }

    mapCurriculumValues() {
        this.curriculumId = this.curriculum.id;
        this.country = this.countryList.find(item => item.sortname === this.curriculum.country).name;
        this.startDate = this.addSlashBetweenDateMonth(this.curriculum.startDate);
        this.endDate = this.addSlashBetweenDateMonth(this.curriculum.endDate);
    }
    
    //Adding slash in start date and end date 
    addSlashBetweenDateMonth(dateValue: string): string {
        let date, month, dateWithSlash;
        date = dateValue.substring(0, 2);
        month = dateValue.substring(2, 4);
        dateWithSlash = date + "/" + month;
        return dateWithSlash;
    }

    public onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj.router.navigateByUrl('/curriculum');
                    localObj.pluginService.sideMenuToggle();
                }
            });
    }
}


