import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { CurriculumService } from './curriculum.service';
import { ICurriculum } from '../shared/model/content/curriculum.model';
import { Subscription, Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { DRAFT, ACTIVE, INACTIVE, ARCHIVED } from '../shared/constants/common.constants'
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { IOrganisation } from '../shared/model/common/organisation.model';
import { FormGroup, FormBuilder } from '@angular/forms';

declare var $: any;
declare var swal: any;

@Component({
  templateUrl: "./curriculum-manage.component.html",
  providers: [CurriculumService]
})
export class CurriculumManageComponent implements OnInit, OnDestroy {
  curriculums: ICurriculum[];
  eventSubscriber: Subscription;
  startDate: string;
  endDate: string;
  itemsPerPage: number;
  totalItems: number;
  totalPages: number;
  currentAccount: any;
  predicate: any;
  reverse: any;
  links: any;
  page: any;

  constructor(
    public _router: Router,
    public curriculumService: CurriculumService,
    protected eventManager: JhiEventManager,
    protected parseLinks: JhiParseLinks,
    protected principalService: Principal,
    protected pluginService: PluginService
  ) {
    this.curriculums = [];
    this.itemsPerPage = ITEMS_PER_PAGE;
    this.page = 0;
    this.links = {
      last: 0
    };
    this.predicate = 'id';
    this.reverse = false;
  }

  loadStandardCurriculums() {
    this.curriculumService
      .getStandardCurriculums({
        page: this.page,
        size: this.itemsPerPage,
        sort: this.sort()
      })
      .subscribe(
        (res: HttpResponse<ICurriculum[]>) => this.paginateCurriculums(res.body, res.headers),
        (res: HttpErrorResponse) => this.onError(res.message)
      );
  }

  protected paginateCurriculums(data: ICurriculum[], headers: HttpHeaders) {
    //this.links = this.parseLinks.parse(headers.get('link'));
    this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        this.curriculums.push(data[i]);
        this.startDate = this.addSlashBetweenDateMonth(data[i].startDate);
        this.endDate = this.addSlashBetweenDateMonth(data[i].endDate);
      }
    } else {
      this.pluginService.alertMessage("No data found..!", "warning");
    }

  }

  reset() {
    this.page = 0;
    this.curriculums = [];
    this.loadStandardCurriculums();
  }

  loadPage(page) {
    this.page = page;
    this.loadStandardCurriculums();
  }

  ngOnInit() {
    this.loadStandardCurriculums();
    this.principalService.identity().then(account => {
      this.currentAccount = account;
    });
    this.registerChangeInCurriculums();
  }

  addSlashBetweenDateMonth(dateValue: string): string {
    let date, month, dateWithSlash;
    date = dateValue.substring(0, 2);
    month = dateValue.substring(2, 4);
    dateWithSlash = date + "/" + month;
    return dateWithSlash;
  }

  ngOnDestroy() {
    this.eventManager.destroy(this.eventSubscriber);
  }

  trackId(index: number, item: ICurriculum) {
    return item.id;
  }

  registerChangeInCurriculums() {
    this.eventSubscriber = this.eventManager.subscribe('curriculumListModification', response => this.reset());
  }

  sort() {
    const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
    if (this.predicate !== 'id') {
      result.push('id');
    }
    return result;
  }

  protected convertToCurriculumList(data: ICurriculum) {
    this.curriculums.push(data);
  }

  protected onError(errorMessage: string) {
    this.pluginService.alertMessage(errorMessage, "error");
  }

  ngDestroy() {
    this.eventSubscriber.unsubscribe();
  }
}